import React, {Component} from 'react';
import {FormLayout, Group, List, ListItem, Panel, PanelHeader, Textarea, View, Input, Button, HeaderButton} from '@vkontakte/vkui';
import * as VKConnect from '@vkontakte/vkui-connect';
import '@vkontakte/vkui/dist/vkui.css';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import { platform, ANDROID, IOS } from '@vkontakte/vkui/dist/lib/platform';


const osname = platform();

export default class App extends Component {

    constructor(props) {
        super(props);

        this.state = {
            activePanel: "main",
            eventData: "ClientAPI Response",
            eventName: "Response",
            events: [
                "VKWebAppAddToFavorites",
                "VKWebAppGetFriends",
                "VKWebAppAddToCommunity",
                "VKWebAppGetCommunityAuthToken",
                "VKWebAppFlashSetLevel",
                "VKWebAppFlashGetInfo",
                "VKWebAppOpenContacts",
                "VKWebAppGetPersonalCard",
                "VKWebAppResizeWindow",
                "VKWebAppScroll",
                "VKWebAppGetAuthToken",
                "VKWebAppCallAPIMethod",
                "VKWebAppGetGeodata",
                "VKWebAppGetUserInfo",
                "VKWebAppGetPhoneNumber",
                "VKWebAppGetClientVersion",
                "VKWebAppOpenPayForm",
                "VKWebAppShare",
                "VKWebAppAllowNotifications",
                "VKWebAppDenyNotifications",
                "VKWebAppShowWallPostBox",
                "VKWebAppGetEmail",
                "VKWebAppAllowMessagesFromGroup",
                "VKWebAppJoinGroup",
                "VKWebAppOpenApp",
                "VKWebAppOpenQR",
                "VKWebAppSetViewSettings",

            ],
            historyApi: []
        };
    }

    componentWillMount() {
        VKConnect.subscribe((e) => {
            const detail = (e.detail || e);
            const type = detail.type;
            const msg = detail.data;

            // History Response
            let currentTime = new Date().toLocaleString();
            this.setState({
                historyApi: [{method: type, response: JSON.stringify(msg, undefined, 4), time: currentTime}, ...this.state.historyApi]
            });

            if (type === "VKWebAppUpdateConfig") {
                if (typeof msg.scheme !== 'undefined') {
                    document.body.setAttribute('scheme', msg.scheme);
                }
            }

            if (['VKWebAppUpdateInfo', 'VKWebAppUpdateInsets'].indexOf(type) === -1) {
                this.setState({
                    eventData: JSON.stringify(msg, undefined, 4),
                    eventName: "Response (" + type + ")",
                });
            }
        });
    }

    render() {
        return (
            <View activePanel={this.state.activePanel}>
                <Panel id="main">
                    <PanelHeader>
                        VK Connect Custom 5.0.1
                    </PanelHeader>
                    <Group title="Data">
                        <FormLayout>
                            <Textarea id='data' placeholder='{"status": "success", "payload": "blabla"}'/>
                        </FormLayout>
                    </Group>

                    <Group title="Custom Event">
                        <FormLayout>
                            <Input id='customEvent' type="text" autocomplete="off" placeholder="VKWebAppOpen"/>
                            <Button onClick={() => {
                                // Send Custom Event
                                let data = {};
                                try {
                                    let input = document.getElementById('data').value;
                                    let customEvent = document.getElementById('customEvent').value;

                                    if (input.length > 0) {
                                        data = JSON.parse(input);
                                    }

                                    VKConnect.send(customEvent, data);
                                } catch (e) {
                                    alert(e);
                                }
                            }}>Send Custom Event</Button>

                            <Button onClick={() => {
                                let customEvent = document.getElementById('customEvent').value;
                                this.setState({events: [customEvent, ...this.state.events]});
                            }}>Add to eventList (only local)</Button>
                        </FormLayout>
                    </Group>

                    <Group title={this.state.eventName}>
                        <FormLayout>
                            <Textarea id='response' value={this.state.eventData}/>
                        </FormLayout>
                    </Group>

                    <Group title="Event type">
                        <FormLayout>
                            <Button size="xl" level="secondary" onClick={() => {
                                this.setState({activePanel: "history"});
                            }}>Show History Response</Button>
                        </FormLayout>
                        <List>
                            {/*<ListItem onClick={() => {*/}
                                {/*VKConnect.send("VKWebAppOpenApp", {"app_id": 6724885});*/}
                            {/*}}>*/}
                                {/*Open This App*/}
                            {/*</ListItem>*/}

                            {/*<ListItem onClick={() => {*/}
                                {/*VKConnect.send("VKWebAppGetPersonalCard", {"type": "all"});*/}
                            {/*}}>*/}
                                {/*VKWebAppGetPersonalCard type all*/}
                            {/*</ListItem>*/}

                            {/*<ListItem onClick={() => {*/}
                                {/*VKConnect.send("VKWebAppClose", {});*/}
                            {/*}}>*/}
                                {/*VKWebAppClose*/}
                            {/*</ListItem>*/}

                            {/*<ListItem onClick={() => {*/}
                                {/*VKConnect.send("VKWebAppClose", {"status": "success"});*/}
                            {/*}}>*/}
                                {/*VKWebAppClose_Success*/}
                            {/*</ListItem>*/}

                            {/*<ListItem onClick={() => {*/}
                                {/*VKConnect.send("VKWebAppClose", {"status": "failed"});*/}
                            {/*}}>*/}
                                {/*VKWebAppClose_Failed*/}
                            {/*</ListItem>*/}

                            {/*<ListItem onClick={() => {*/}
                                {/*VKConnect.send("VKWebAppClose", {"status": "success", "payload": "blabla"});*/}
                            {/*}}>*/}
                                {/*VkWebAppClose_Success_Payload*/}
                            {/*</ListItem>*/}

                            {/*<ListItem onClick={() => {*/}
                                {/*VKConnect.send("VKWebAppClose", {"status": "success", "payload": {"test1": "test2"}});*/}
                            {/*}}>*/}
                                {/*VkWebAppClose_Success_Payload Object*/}
                            {/*</ListItem>*/}

                            {
                                this.state.events.map(function (eventName) {
                                    return (
                                        <ListItem onClick={() => {
                                            let data = {};
                                            try {
                                                let input = document.getElementById('data').value;
                                                if (input.length > 0) {
                                                    data = JSON.parse(input);
                                                }

                                                VKConnect.send(eventName, data);
                                            } catch (e) {
                                                alert(e);
                                            }
                                        }
                                        }>
                                            {eventName}
                                        </ListItem>
                                    );
                                })
                            }
                        </List>
                    </Group>
                </Panel>

                <Panel id="history">
                    <PanelHeader
                        left={<HeaderButton onClick={() => this.setState({ activePanel: 'main' })}>{osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}</HeaderButton>}
                        addon={<HeaderButton onClick={() => this.setState({ activePanel: 'main' })}>Назад</HeaderButton>}
                    >
                        Response History
                    </PanelHeader>

                    {
                        this.state.historyApi.map(function (eventName) {
                            if(eventName["method"] && eventName["method"].length) {
                                return (
                                    <Group title={eventName["method"]} description={eventName["time"]}>
                                        <FormLayout>
                                            <Textarea value={eventName["response"]}/>
                                        </FormLayout>
                                    </Group>
                                );
                            }
                        })
                    }
                </Panel>
            </View>
        );
    }
}



